module.exports = " <div class=main_wrap _v-44bed7f8=\"\"> <div class=\"main pd06\" _v-44bed7f8=\"\"> <div class=main_header _v-44bed7f8=\"\"> <h1 _v-44bed7f8=\"\">大会概况</h1> </div> <div class=main_con _v-44bed7f8=\"\"> <div class=main_con_txt _v-44bed7f8=\"\"> <p _v-44bed7f8=\"\">2016年是“十三五”的开局之年，是实现2017年深化医药卫生体制改革阶段性目标的攻坚之年，也是实现2020年人人享有基本医疗卫生服务目标的关键之年。</p> <p _v-44bed7f8=\"\">国务院在《深化医药卫生体制改革2016年重点工作任务》中，对卫生人才队伍建设提出更高的要求。整个医疗卫生体系的发展、医疗质量的提升、推进人民群体满意的医改，均以优秀的医疗卫生人才为根基。</p> <p _v-44bed7f8=\"\">然而，因为培养模式和地域差异，中国的医生群体异常割裂，无论是从能力、学历、实践技巧、理论水平都千差万别。推行分级诊疗，困难重重。</p> <p _v-44bed7f8=\"\">只有显著提高基层医生的诊疗水平，患者才会回归基层；才能为完善“分级诊疗”、“双向转诊”制度，实现“大病不出县”等政策目标提供赖以落地的资源基础；才能为响应“健康中国”的宏伟战略而颁布的推进医疗改革、提升医疗质量的政策措施保驾护航。</p> <p _v-44bed7f8=\"\">因而，建立与完善适应行业特点的医学人才培养制度，培养人民满意的好医生，已是深化医改的一项重要任务，也是医学教育改革与发展的方向和目标。</p> <p _v-44bed7f8=\"\">“2016中国医疗质量提升大会”将以打造可持续发展的医疗体系为己任，以“触及医疗核心--强化基层服务质量水平”为主题，聚集全国医疗业界权威专家学者，聚焦分级诊疗、住院医师规范化培训、基层人才强化战略、医患沟通以及各国的医疗管理模式分享，探求“互联网+”、生态医疗和医疗人工智能在现代化卫生人才建设中的影响和机会。</p> <p _v-44bed7f8=\"\">同时，大会将通过传递白衣天使正能量为基调，形成医生敬畏生命，患者尊重医生的和谐沟通平台，以大型公益项目“好医帮好人”贯穿活动始终，弘扬广德济世、博爱康民的大爱精神。</p> <p _v-44bed7f8=\"\">在此，大会特邀医疗产业界同仁，莅临大会考察指导，共谋“中国医疗质量提升”之大业。</p> <p _v-44bed7f8=\"\">湘江北去，橘子洲头。聚贤长沙，共襄盛举！</p> </div> <span class=bottom_bg _v-44bed7f8=\"\"></span> </div> <div class=video_bg _v-44bed7f8=\"\"> <a href=http://www.mycs.cn/player/v268161.html _v-44bed7f8=\"\"><img src=" + require("../assets/images/video.png") + " _v-44bed7f8=\"\"></a> </div> </div> </div> ";


