<template>
  <div>
    <Menu v-on:show-menu="menuShow"></Menu>
    <section :class="{show:isShow}" >
      <header>
        <i class="icon iconfont"  @click="showMenu()" id="menu-a">{{ icon }}</i>
        <h1>{{ title }}</h1>
      </header>
      <router-view></router-view>
      <Tab></Tab>
    </section>
  </div>
        

</template>
<style src="./assets/css/reset.css"></style>
<style scoped>
  section{
    background-color: #fff;
    left: 0;
    min-height: 100%;
    overflow-x: hidden;
    position: relative;
    width: 100%;
    transition: all .3s ease-in;
  }
  section.show{ left: 42%;}
</style>
<script>
  import Menu from './components/Menu'
  import Tab from './components/TabBar'
  (function () {
    document.addEventListener('DOMContentLoaded', function () {
        var html = document.documentElement;
        var windowWidth = html.clientWidth;
        html.style.fontSize = windowWidth / 6.4 + 'px';
    }, false);
  })();
  export default {
    data(){
      return {
        title: "中国医疗质量提升大会",
        icon: "",
        isShow: false
      }
    },
    components:{Menu,Tab},
    methods:{
      showMenu () {
        this.isShow = !this.isShow;
        //使用 $broadcast() 广播事件，事件向下传导给所有的后代
        this.$broadcast('broadcast',this.isShow)
      },
      menuShow (show) {
        this.isShow = show;
      }
    }
  }
</script>



