<template>
  <nav id="menu" :class="{show:isShow}">
    <ul>
      <li v-for="link in links">
        <a v-link="link.url" @click="home" >{{ link.txt }}</a>
      </li>
    </ul>
  </nav>
</template>
<style lang="scss">
  nav{
    background: #fff none repeat scroll 0 0;height: 100%;left: 0;overflow: hidden;position: fixed;width: 100%;z-index: 0;opacity: 0;transition: all .3s ease-in;
    ul{width: 42%; height: 100%;box-shadow: -10px -2px 20px #ddd inset;
      li{ text-align: center; border-bottom: 1px solid #eee;} 
    }
    a{color: #666; font-size: .30rem; display: block; height: .8rem; line-height: .8rem;}
  }
  nav.show{ opacity:1;}

</style>
<script>
  export default {
    data () {
      return {
        isShow: false,
        links:[
          {url:"./home",txt:"首　　页"},
          {url:"./dhgk",txt:"大会概况"},
          {url:"./nyjb",txt:"拟邀嘉宾"},
          {url:"./dhrc",txt:"大会日程"},
          {url:"./wych",txt:"我要参会"},
          {url:"./chzn",txt:"参会指南"},
          {url:"./sdlm",txt:"盛典栏目"},
          {url:"./xwdt",txt:"新闻动态"},
          {url:"./wyhz",txt:"我要合作"}
        ]
      }
    },
    events:{
      broadcast (show) {
        this.isShow=show;
      }
    },
    methods:{
      home () {
        this.isShow=!this.isShow;
        this.$emit('show-menu',this.isShow);
      }
    }
  }
</script>


