import Vue from 'vue'
import App from './App'
import Home from './components/Home'
import Dhgk from './components/Dhgk'
import Nyjb from './components/Nyjb'
import Dhrc from './components/Dhrc'

import Chzn from './components/Chzn'
import Sdlm from './components/Sdlm'
import Xwdt from './components/Xwdt'
import Wyhz from './components/Wyhz'

import VueRouter from 'vue-router'

//注册插件
Vue.use(VueRouter)

const router = new VueRouter()

//路由map
router.map({
  '/home': {
    component: Home
  },
  '/dhgk': {
    component: Dhgk
  },
  '/nyjb': {
    component: Nyjb
  },
  '/dhrc': {
    component: Dhrc
  },
  '/chzn': {
    component: Chzn
  },
  '/sdlm': {
    component: Sdlm
  },
  '/xwdt': {
    component: Xwdt
  },
  '/wyhz': {
    component: Wyhz
  }
})

router.redirect({
  '*': '/home'
})

router.start(App, '#app')




