	module.exports = " <div class=main_wrap _v-0517f52e=\"\"> <div class=\"main pd06\" _v-0517f52e=\"\"> <div class=main_header _v-0517f52e=\"\"> <h1 _v-0517f52e=\"\">参会指南</h1> </div> <div class=\"main_con pd02\" _v-0517f52e=\"\"> <div class=header_img _v-0517f52e=\"\"> <img src=" + __webpack_require__(15) + " _v-0517f52e=\"\"> </div> <div class=chznBox _v-0517f52e=\"\"> <h1 _v-0517f52e=\"\">酒店</h1> <div class=chznBox_con _v-0517f52e=\"\"> <h2 _v-0517f52e=\"\">圣爵菲斯大酒店</h2> <p _v-0517f52e=\"\">地址：长沙市开福区浏阳河大桥东金鹰影视文化城</p> <p _v-0517f52e=\"\">电话：0731-84252333</p> </div> <div class=chznBox_con _v-0517f52e=\"\"> <h2 _v-0517f52e=\"\">机场火车站</h2> <p _v-0517f52e=\"\">距黄花机场约28公里，打车约33分钟</p> <p _v-0517f52e=\"\">距长沙火车站约8公里，打车约26分钟</p> <p _v-0517f52e=\"\">距长沙东站约10.6公里，打车约27分钟</p> <p _v-0517f52e=\"\">距长沙南站（高铁）约15公里，打车约29分钟</p> </div> </div> <div class=chznBox _v-0517f52e=\"\"> <h1 _v-0517f52e=\"\">交通</h1> <div class=chznBox_con _v-0517f52e=\"\"> <h2 _v-0517f52e=\"\">黄花机场</h2> <p _v-0517f52e=\"\">磁浮快线：（磁浮机场站——磁浮高铁站B1出口）——步行500米——503路：长沙火车南站——芙蓉区政府站——步行300米——915路：芙蓉区政府站——世界之窗站——步行800米（圣爵菲斯大酒店）</p> </div> <div class=chznBox_con _v-0517f52e=\"\"> <h2 _v-0517f52e=\"\">长沙火车站</h2> <p _v-0517f52e=\"\">158路：长沙火车站——世界之窗站——步行800米（圣爵菲斯大酒店）</p> </div> <div class=chznBox_con _v-0517f52e=\"\"> <h2 _v-0517f52e=\"\">长沙东站</h2> <p _v-0517f52e=\"\">“阿弥岭站”乘坐915路——世界之窗站——步行800米（圣爵菲斯大酒店）</p> </div> <div class=chznBox_con _v-0517f52e=\"\"> <h2 _v-0517f52e=\"\">长沙南站</h2> <p _v-0517f52e=\"\">地址2号线：长沙火车南站——万家丽广场站2出口——步行300米——915路：万家丽广场站——世界之窗站——步行800米（圣爵菲斯大酒店）</p> </div> </div> <span class=bottom_bg _v-0517f52e=\"\"></span> </div> </div> </div> ";

