	module.exports = " <ul class=tab _v-57136ac5=\"\"> <li class=on _v-57136ac5=\"\">11月18日</li> <li class=\"\" _v-57136ac5=\"\">11月19日</li> <li class=\"\" _v-57136ac5=\"\">11月20日</li> </ul> <div class=main_wrap _v-57136ac5=\"\"> <div class=\"main pd06\" _v-57136ac5=\"\"> <div class=main_header _v-57136ac5=\"\"> <h1 _v-57136ac5=\"\">大会日程</h1> </div> <div class=main_con _v-57136ac5=\"\"> <ul class=schedule style=\"\" _v-57136ac5=\"\"> <li _v-57136ac5=\"\"> <span _v-57136ac5=\"\">08:30~09:00</span> <div _v-57136ac5=\"\"> <em _v-57136ac5=\"\"></em> <p _v-57136ac5=\"\">开幕式</p> </div> </li> <li _v-57136ac5=\"\"> <span _v-57136ac5=\"\">09:00~12:00</span> <div _v-57136ac5=\"\"> <em _v-57136ac5=\"\"></em> <p _v-57136ac5=\"\">主论坛：触及医疗核心——强化基层服务质量水平</p> </div> </li> <li _v-57136ac5=\"\"> <span _v-57136ac5=\"\">12:00~14:00</span> <div _v-57136ac5=\"\"> <em _v-57136ac5=\"\"></em> <p _v-57136ac5=\"\">午餐及自由交流</p> </div> </li> <li _v-57136ac5=\"\"> <span _v-57136ac5=\"\">14:00~17:30</span> <div _v-57136ac5=\"\"> <em _v-57136ac5=\"\"></em> <p _v-57136ac5=\"\"> 分论坛一：强基层，惠大众——全科医师规范化培训现状、问题及对策研究<br _v-57136ac5=\"\"> 分论坛二：现代医院管理模式探讨—完善制度与建立配套体制及机制<br _v-57136ac5=\"\"> 分论坛三：探索·生态医疗——互联网+医疗的开放与融合<br _v-57136ac5=\"\"> 分论坛四：医生集团的孵化与成长</p> </div> </li> <li _v-57136ac5=\"\"> <span _v-57136ac5=\"\">17:30~19:30</span> <div _v-57136ac5=\"\"> <em _v-57136ac5=\"\"></em> <p _v-57136ac5=\"\">晚餐及自由交流</p> </div> </li> <li _v-57136ac5=\"\"> <span _v-57136ac5=\"\">19:30~21:30</span> <div _v-57136ac5=\"\"> <em _v-57136ac5=\"\"></em> <p _v-57136ac5=\"\">“好医帮好人”公益盛典</p> </div> </li> </ul> <ul class=schedule style=\"display: none\" _v-57136ac5=\"\"> <li _v-57136ac5=\"\"> <span _v-57136ac5=\"\">08:30~12:00</span> <div _v-57136ac5=\"\"> <em _v-57136ac5=\"\"></em> <p _v-57136ac5=\"\"> 分论坛五：分级诊疗与强基层的战略构想<br _v-57136ac5=\"\"> 分论坛六：颠覆·改革——住院医师/专科医师规范化培训模式和标准的探讨<br _v-57136ac5=\"\"> 分论坛七：沃土藏情怀——医疗投融资现状与前景<br _v-57136ac5=\"\"> 分论坛八：人类事业·医疗无边界——两岸三地及海外医疗管理经验分享<br _v-57136ac5=\"\"> 分论坛九：善用互联网医疗媒体舆论导向的力量</p> </div> </li> <li _v-57136ac5=\"\"> <span _v-57136ac5=\"\">12:00~14:00</span> <div _v-57136ac5=\"\"> <em _v-57136ac5=\"\"></em> <p _v-57136ac5=\"\">午餐及自由交流</p> </div> </li> <li _v-57136ac5=\"\"> <span _v-57136ac5=\"\">14:00~17:30</span> <div _v-57136ac5=\"\"> <em _v-57136ac5=\"\"></em> <p _v-57136ac5=\"\"> 分论坛十：打破空间、时间的限制，构建临床诊治的桥梁——建设远程会诊体系<br _v-57136ac5=\"\"> 分论坛十一：模拟与虚拟现实的较量<br _v-57136ac5=\"\"> 分论坛十二：规范、专业、标准、精细--加强医院医疗质量管理与控制<br _v-57136ac5=\"\"> 分论坛十三：源远·流长——共商中医传承发展大计<br _v-57136ac5=\"\"> 分论坛十四：临床治疗出发点新定义——中国“精准医疗计划”</p> </div> </li> <li _v-57136ac5=\"\"> <span _v-57136ac5=\"\">17:30~19:30</span> <div _v-57136ac5=\"\"> <em _v-57136ac5=\"\"></em> <p _v-57136ac5=\"\">晚餐及自由交流</p> </div> </li> </ul> <ul class=schedule style=\"display: none\" _v-57136ac5=\"\"> <li _v-57136ac5=\"\"> <span _v-57136ac5=\"\">08:30~11:30</span> <div _v-57136ac5=\"\"> <em _v-57136ac5=\"\"></em> <p _v-57136ac5=\"\">分论坛十五：医院发展战略与强化学科建设——综合医院的专科建设<br _v-57136ac5=\"\"> 分论坛十六：以人为本——医患沟通的艺术<br _v-57136ac5=\"\"> 分论坛十七：主流之外的未来畅想——非公立医疗机构专场<br _v-57136ac5=\"\"> 分论坛十八：医疗机构的品牌建设及管理<br _v-57136ac5=\"\"> 分论坛十九：加强医德医风建设，构建和谐医患关系</p> </div> </li> <li _v-57136ac5=\"\"> <span _v-57136ac5=\"\">11:30~12:00</span> <div _v-57136ac5=\"\"> <em _v-57136ac5=\"\"></em> <p _v-57136ac5=\"\">闭幕式</p> </div> </li> <li _v-57136ac5=\"\"> <span _v-57136ac5=\"\">12:00~13:30</span> <div _v-57136ac5=\"\"> <em _v-57136ac5=\"\"></em> <p _v-57136ac5=\"\">午餐及返程</p> </div> </li> </ul> <span class=bottom_bg _v-57136ac5=\"\"></span> </div> </div> </div> ";

